let title = ''
let content = ''
const popover = (
    <Popover id="popover-basic">
        <Popover.Title as="h3">'Good'</Popover.Title>
        <Popover.Content>
            {content}
        </Popover.Content>
    </Popover>
);

const OverlayPopover = (props) => {
    title = props.title
    content = props.content
    return (
        <OverlayTrigger trigger="hover" placement="bottom" delay={{ show: 200, hide: 100 }} overlay={popover}>
            {props.children}
        </OverlayTrigger>
    )
}

export default OverlayPopover